%font_title{
    font-family:Helvetica,Arial,sans-serif;
    font-weight: 700;
    font-size: 20px;
    color: #2e2f32;
}
%font_subtitle{
    font-family:Helvetica,Arial,sans-serif;
    font-weight: 100;
    font-size: 16px;
    color: #46474a;
}
.main-container-content__summer-saving{
    margin: 24px 0;
    padding-bottom: 24px;
    border-bottom: 1px solid #f1f1f2;
    .main-container-content__summer-saving_title{
        @extend %font_title
    }
    .main-container-content__summer-saving__links{
        display: flex;
        justify-content: space-between;
        margin-top: 16px;
        .main-container-content__summer-saving__links_icon-img{
            width: 100%;
            height: auto
        }
        .main-container-content__summer-saving__links_icon-text{
            @extend %font_subtitle;
            text-align: center;
            margin-top: 10px;
        }
    }
}
.main-container-content__favorite{
    margin: 24px 0;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    .main-container-content__favorite_title{
        @extend %font_title;
        margin-bottom: 16px;
    }
    .main-container-content__favorite_view{
        font-family:Helvetica,Arial,sans-serif;
        font-weight: 100;
        font-size: 14px;
        color: #2e2f32;
        text-decoration: underline;
    }
    .main-container-content__favorite__links{
        width: 100%;
        display: flex;
        justify-content: space-between;
        .main-container-content__favorite__links_icon{
            width: 182px;
            height: 228px;
            padding-left: 16px;
            box-shadow: 0px 1px 4px 0px rgba(0,0,0,0.53);
            border-radius: 5px;
            display: flex;
            align-items: center;
            &:nth-child(1){
                background-image: url(../img/content/bg1.webp);
            }
            &:nth-child(2){
                background-image: url(../img/content/bg2.png);
            }
            &:nth-child(3){
                background-image: url(../img/content/bg3.webp);
            }
            &:nth-child(4){
                background-image: url(../img/content/bg4.webp);
            }
            &:nth-child(5){
                background-image: url(../img/content/bg5.png);
            }
            &:nth-child(6){
                background-image: url(../img/content/bg6.webp);
            }
            .main-container-content__favorite__links_icon-text{
                font-family:Helvetica,Arial,sans-serif;
                font-weight: 700;
                font-size: 20px;
                color: #004f9a;
            }
        }
    }
    .main-container-content__favorite-cash{
        width: 100%;
        box-shadow: 0px 1px 4px 0px rgba(0,0,0,0.53);
        border-radius: 5px;
        margin: 24px 0;
        padding: 8px 16px;
        display: flex;
        justify-content: center;
        align-items: center;
        .main-container-content__favorite-cash_text{
            @extend %font_subtitle;
            margin-left: 15px;
            .main-container-content__favorite-cash_span{
                font-weight: 700;
                border-bottom: 3px solid rgb(0, 113, 220);
                &:last-child{
                    font-size: 14px;
                    font-weight: 100;
                    color: black;
                    border-bottom: 1px solid black;
                    margin-left: 16px;
                }
            }
        }
    }
}
@keyframes scroll{
    5%{
        transform: translate(-620%);
    }
    20%{
        transform: translate(-620%);
    }
    25%{
        transform: translate(-930%);
    }
    60%{
        transform: translate(-930%);
    }
    65%{
        transform: translate(0);
    }
    100%{
        transform: translate(0); 
    }
}
@keyframes scroll-short{
    5%{
        transform: translate(-620%);
    }
    20%{
        transform: translate(-620%);
    }
    25%{
        transform: translate(-730%);
    }
    60%{
        transform: translate(-730%);
    }
    65%{
        transform: translate(0);
    }
    100%{
        transform: translate(0); 
    }
}
.main-container-content__save-big{
    width: 100%;
    margin: 24px 0;    
    .main-container-content__save-big_title{
        margin-bottom: 16px;
        @extend %font_title;
    }
    .main-container-content__save-big__content{
        display: flex;
        justify-content: space-between;
        overflow-x: hidden;      
        .main-container-content__save-big__content-block{
            min-width: 16%;
            margin: 0 4px;
            animation-name: scroll;
            animation-duration: 20s;
            animation-timing-function: linear;
            animation-iteration-count: infinite;               
            animation-delay: 0s;
            animation-play-state: running;
            &hover{
                animation-play-state: paused;
            }          
            .main-container-content__save-big__content-block__stars{
                display: flex;
                flex-wrap: nowrap;
                width: 100%;
                height: 12px;
                .main-container-content__save-big__content-block__stars_text{
                    margin-left: 5px;
                    font-family:Helvetica,Arial,sans-serif;
                    font-size: 14px;
                    color: #74767c;
                }
            }
            .main-container-content__save-big__content-block_img{
                object-fit: contain;
                width: 177px;
                height: 177px;
            }
            .main-container-content__save-big__content-block_img-hidden{
                display: none;
            }
            .main-container-content__save-big__content-block_title{
                @extend %font_title;
                margin-top: 10px;
            }           
            .main-container-content__save-big__content-block_title-old{
                font-size: 14px;
                color: #74767c;
                text-decoration: line-through;
                margin-left: 10px
            }
            .main-container-content__save-big__content-block_subtitle{
                @extend %font_subtitle;
                margin-top: 15px;
                line-height: 1.3;
            }
            .main-container-content__save-big__content-block__stars{
                margin-top: 10px;
            }
            .main-container-content__save-big__content-block__menu{
                display: flex;
                justify-content: center;
                height: 30px;
                margin-top: 24px;
                margin-bottom: 4px;
                .main-container-content__save-big__content-block_button{
                    width: 77px;
                    height: 40px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    background-color: #0071dc;
                    font-family:Helvetica,Arial,sans-serif;
                    font-weight: 700;
                    font-size: 20px;                    
                    color: white;
                    border-radius: 50px;
                    position: relative;
                    bottom: 45px;
                    right: 60px;
                    padding: 0 8px;
                    font-size: 16px;                    
                    &:hover{
                        background: #004f9a;
                    }
                    .main-container-content__save-big__content-block_button-icon{
                        fill: white;
                        width: 16px;
                        height: auto;
                        margin-right: 5px;
                    }
                }
                .main-container-content__save-big__content-block__menu_icon{
                    border-radius: 100%;
                    margin: 0 10px;
                    width: 26.7px;
                    height: 26.7px;                    
                    background-size: cover;
                    &:hover{
                        border: 1px solid black;
                    }
                }
                .main-container-content__save-big__content-block__menu_icon_pic-black{
                    background-image: url(../img/content-pics/1-black.png);
                }
                .main-container-content__save-big__content-block__menu_icon_pic-white{
                    background-image: url(../img/content-pics/1-white.webp);
                }
                .main-container-content__save-big__content-block__menu_icon_black{
                    background-image: url(../img/content-pics/3-black.jpeg);
                }
                .main-container-content__save-big__content-block__menu_icon_white{
                    background-image: url(../img/content-pics/3-white.jpeg);
                }
                .main-container-content__save-big__content-block__menu_icon_pink{
                    background-image: url(../img/content-pics/6-pink.webp);
                }
                .main-container-content__save-big__content-block__menu_icon_blue{
                    background-image: url(../img/content-pics/6-blue.webp);
                }                 
            }
            .main-container-content__save-big__content-block__info{
                display: flex;
                flex-wrap: wrap;
                margin: 8px 0;                
                .main-container-content__save-big__content-block__info_text{
                    display: flex;
                    align-items: center;
                    padding: 4px 4px;
                    margin-bottom: 4px;
                    margin-right: 4px;
                    background: #e6f1fc;
                    color: #004f9a;                    
                    text-align: center;
                    height: fit-content;
                    font-family:Helvetica,Arial,sans-serif;
                    font-weight: 100;
                    font-size: 14px;                
                }
            }
        }
        .main-container-content__save-big__content-block_animation-none{
            animation-name: none;
        }
        .main-container-content__save-big__content-block_animation-short{
            animation-name: scroll-short;
        } 
    }
    .main-container-content__save-big__content-hidden{
        overflow-x: hidden;
    }
}
.main-container-content__foryou{
    margin: 24px 0;
    .main-container-content__foryou_title{
        @extend %font_title;
        margin-bottom: 16px;
    }
    .main-container-content__foryou__links{
        display: flex;
        justify-content: space-between;
        .main-container-content__foryou__links-icon{
            box-shadow: 0px 1px 4px 0px rgba(0,0,0,0.53);
            border-radius: 5px;
            .main-container-content__foryou__links-icon_img{
                overflow: hidden;
                border-top-left-radius: 5px;
                border-top-right-radius: 5px;
                max-width: 100%;
            }
            .main-container-content__foryou__links-icon_title{
                margin: 16px 16px;
                @extend %font_title
            }
            .main-container-content__foryou__links-icon_subtitle{
                margin: 16px 16px;
                @extend %font_subtitle
            }
            .main-container-content__foryou__links-icon_button{
                margin: 16px 16px;
                height: 30px;
                border: 1px solid black;
                border-radius: 50px;
                background-color: white;
                padding: 0 16px;
                font-family:Helvetica,Arial,sans-serif;
                font-weight: 700;
                font-size: 14px;
                color: black;
                &:hover{
                    background-color: black;
                    color: white;
                }
            }
        }
    }
}
.main-container-content_link{
    font-family:Helvetica,Arial,sans-serif;
    font-weight: 100;
    font-size: 14px;
    color: #2e2f32;
    text-decoration: underline;
    margin: 24px 0;
}
